import { useState, useEffect } from 'react';
import {
  Search, Filter, TrendingUp, Key, Copy, CheckCircle, ExternalLink,
  Code, Package, Zap, Shield, Clock, DollarSign, Activity, Plus, X
} from 'lucide-react';
import { db } from '../../lib/db';
import { useAuth } from '../../contexts/AuthContext';

interface Industry {
  id: string;
  industry_name: string;
  industry_code: string;
  description: string;
  icon_name: string;
  display_order: number;
}

interface APIProduct {
  id: string;
  industry_id: string;
  product_name: string;
  product_code: string;
  description: string;
  api_endpoint: string;
  pricing_model: string;
  base_price: number;
  price_per_request: number;
  rate_limit_per_minute: number;
  rate_limit_per_day: number;
  features: string[];
}

interface APIKey {
  id: string;
  product_id: string;
  key_name: string;
  api_key: string;
  api_secret: string;
  environment: string;
  is_active: boolean;
  total_requests: number;
  created_at: string;
}

export default function APIMarketplace() {
  const { user } = useAuth();
  const [activeTab, setActiveTab] = useState<'marketplace' | 'my-keys'>('marketplace');
  const [selectedIndustry, setSelectedIndustry] = useState<string>('all');
  const [searchQuery, setSearchQuery] = useState('');

  const [industries, setIndustries] = useState<Industry[]>([]);
  const [apiProducts, setAPIProducts] = useState<APIProduct[]>([]);
  const [myKeys, setMyKeys] = useState<APIKey[]>([]);
  const [loading, setLoading] = useState(true);

  const [generateKeyModal, setGenerateKeyModal] = useState<APIProduct | null>(null);
  const [newKeyName, setNewKeyName] = useState('');
  const [selectedEnvironment, setSelectedEnvironment] = useState<'production' | 'sandbox'>('production');
  const [generating, setGenerating] = useState(false);

  const [notification, setNotification] = useState<{ type: 'success' | 'error'; message: string } | null>(null);
  const [copiedKey, setCopiedKey] = useState<string | null>(null);

  useEffect(() => {
    if (user) {
      loadData();
    }
  }, [user]);

  useEffect(() => {
    if (notification) {
      const timer = setTimeout(() => setNotification(null), 5000);
      return () => clearTimeout(timer);
    }
  }, [notification]);

  const loadData = async () => {
    setLoading(true);
    await Promise.all([
      loadIndustries(),
      loadAPIProducts(),
      loadMyKeys()
    ]);
    setLoading(false);
  };

  const loadIndustries = async () => {
    const { data, error } = await db
      .from('api_marketplace_industries')
      .select('*')
      .eq('is_active', true)
      .order('display_order');

    if (!error && data) {
      setIndustries(data);
    }
  };

  const loadAPIProducts = async () => {
    const { data, error } = await db
      .from('api_marketplace_products')
      .select('*')
      .eq('is_active', true)
      .order('product_name');

    if (!error && data) {
      setAPIProducts(data);
    }
  };

  const loadMyKeys = async () => {
    if (!user) return;

    const { data, error } = await db
      .from('api_marketplace_keys')
      .select('*')
      .eq('user_id', user.id)
      .order('created_at', { ascending: false });

    if (!error && data) {
      setMyKeys(data);
    }
  };

  const handleGenerateKey = async () => {
    if (!generateKeyModal || !user || !newKeyName.trim()) {
      setNotification({ type: 'error', message: 'Please enter a key name' });
      return;
    }

    setGenerating(true);

    try {
      const apiKey = `${generateKeyModal.product_code.toLowerCase()}_${selectedEnvironment}_${Math.random().toString(36).substr(2, 16)}`;
      const apiSecret = `sec_${Math.random().toString(36).substr(2, 24)}`;

      const { data: tenantData } = await db
        .from('tenant_users')
        .select('tenant_id')
        .eq('user_id', user.id)
        .single();

      if (!tenantData) {
        throw new Error('Tenant not found');
      }

      const { error } = await db
        .from('api_marketplace_keys')
        .insert({
          tenant_id: tenantData.tenant_id,
          user_id: user.id,
          product_id: generateKeyModal.id,
          key_name: newKeyName.trim(),
          api_key: apiKey,
          api_secret: apiSecret,
          environment: selectedEnvironment,
          is_active: true
        });

      if (error) throw error;

      setNotification({ type: 'success', message: 'API key generated successfully!' });
      setGenerateKeyModal(null);
      setNewKeyName('');
      setSelectedEnvironment('production');
      loadMyKeys();
      setActiveTab('my-keys');
    } catch (error: any) {
      setNotification({ type: 'error', message: error.message || 'Failed to generate API key' });
    } finally {
      setGenerating(false);
    }
  };

  const copyToClipboard = (text: string, type: string) => {
    navigator.clipboard.writeText(text);
    setCopiedKey(type);
    setTimeout(() => setCopiedKey(null), 2000);
  };

  const filteredProducts = apiProducts.filter(product => {
    const matchesIndustry = selectedIndustry === 'all' || product.industry_id === selectedIndustry;
    const matchesSearch = product.product_name.toLowerCase().includes(searchQuery.toLowerCase()) ||
                         product.description?.toLowerCase().includes(searchQuery.toLowerCase());
    return matchesIndustry && matchesSearch;
  });

  const getIndustryById = (id: string) => {
    return industries.find(ind => ind.id === id);
  };

  return (
    <div className="flex-1 overflow-auto bg-slate-950">
      <div className="relative h-48 bg-gradient-to-r from-emerald-600 via-teal-600 to-cyan-600 overflow-hidden">
        <div className="absolute inset-0 bg-black/20"></div>
        <div className="relative h-full flex items-center px-8">
          <div className="flex items-center gap-4">
            <div className="bg-white/20 backdrop-blur-sm p-4 rounded-2xl">
              <Package className="w-12 h-12 text-white" />
            </div>
            <div>
              <h1 className="text-4xl font-bold text-white mb-2">API Marketplace</h1>
              <p className="text-white/90 text-lg">Industry-Specific APIs for Every Business Need</p>
            </div>
          </div>
        </div>
      </div>

      <div className="p-8">
        <div className="bg-slate-800/50 border border-slate-700 rounded-xl p-1 mb-8 inline-flex">
          <button
            onClick={() => setActiveTab('marketplace')}
            className={`px-6 py-2 rounded-lg transition-colors ${
              activeTab === 'marketplace' ? 'bg-emerald-600 text-white' : 'text-slate-400 hover:text-white'
            }`}
          >
            Browse APIs
          </button>
          <button
            onClick={() => setActiveTab('my-keys')}
            className={`px-6 py-2 rounded-lg transition-colors ${
              activeTab === 'my-keys' ? 'bg-emerald-600 text-white' : 'text-slate-400 hover:text-white'
            }`}
          >
            My API Keys ({myKeys.length})
          </button>
        </div>

        {activeTab === 'marketplace' && (
          <div className="space-y-6">
            <div className="flex items-center gap-4 flex-wrap">
              <div className="relative flex-1 min-w-[300px]">
                <Search className="w-4 h-4 text-slate-400 absolute left-3 top-1/2 -translate-y-1/2" />
                <input
                  type="text"
                  placeholder="Search APIs..."
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  className="w-full bg-slate-900 border border-slate-700 rounded-lg pl-10 pr-4 py-2 text-white placeholder-slate-500 focus:outline-none focus:ring-2 focus:ring-emerald-500"
                />
              </div>
              <select
                value={selectedIndustry}
                onChange={(e) => setSelectedIndustry(e.target.value)}
                className="bg-slate-900 border border-slate-700 rounded-lg px-4 py-2 text-white focus:outline-none focus:ring-2 focus:ring-emerald-500"
              >
                <option value="all">All Industries</option>
                {industries.map(industry => (
                  <option key={industry.id} value={industry.id}>
                    {industry.industry_name}
                  </option>
                ))}
              </select>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {filteredProducts.map((product) => {
                const industry = getIndustryById(product.industry_id);
                return (
                  <div key={product.id} className="bg-slate-800/50 border border-slate-700 rounded-xl p-6 hover:border-slate-600 transition-all hover:shadow-lg hover:shadow-emerald-500/10">
                    <div className="flex items-start justify-between mb-4">
                      <div className="flex-1">
                        <h3 className="text-lg font-semibold text-white mb-2">{product.product_name}</h3>
                        {industry && (
                          <span className="px-3 py-1 rounded-full text-xs font-medium bg-emerald-500/20 text-emerald-400 border border-emerald-500/30">
                            {industry.industry_name}
                          </span>
                        )}
                      </div>
                      <div className="bg-emerald-500/20 p-2 rounded-lg">
                        <Code className="w-5 h-5 text-emerald-400" />
                      </div>
                    </div>

                    <p className="text-slate-400 text-sm mb-4 line-clamp-2">{product.description}</p>

                    <div className="space-y-2 mb-4">
                      <div className="flex items-center justify-between text-sm">
                        <span className="text-slate-400 flex items-center gap-1">
                          <Zap className="w-3 h-3" />
                          Rate Limit:
                        </span>
                        <span className="text-white font-medium">{product.rate_limit_per_minute}/min</span>
                      </div>
                      <div className="flex items-center justify-between text-sm">
                        <span className="text-slate-400 flex items-center gap-1">
                          <DollarSign className="w-3 h-3" />
                          Pricing:
                        </span>
                        <span className="text-white font-medium">
                          {product.pricing_model === 'subscription'
                            ? `RM ${product.base_price}/mo`
                            : `RM ${product.price_per_request}/req`
                          }
                        </span>
                      </div>
                      <div className="flex items-center justify-between text-sm">
                        <span className="text-slate-400 flex items-center gap-1">
                          <Shield className="w-3 h-3" />
                          Endpoint:
                        </span>
                        <span className="text-cyan-400 font-mono text-xs">{product.api_endpoint.split('/').pop()}</span>
                      </div>
                    </div>

                    <div className="mb-4">
                      <p className="text-slate-300 text-xs font-medium mb-2">Features:</p>
                      <div className="flex flex-wrap gap-1">
                        {(product.features || []).slice(0, 3).map((feature, idx) => (
                          <span key={idx} className="px-2 py-0.5 bg-slate-900/50 text-slate-300 rounded text-xs">
                            {feature}
                          </span>
                        ))}
                        {(product.features || []).length > 3 && (
                          <span className="px-2 py-0.5 bg-slate-900/50 text-slate-400 rounded text-xs">
                            +{(product.features || []).length - 3}
                          </span>
                        )}
                      </div>
                    </div>

                    <button
                      onClick={() => setGenerateKeyModal(product)}
                      className="w-full bg-emerald-600 hover:bg-emerald-700 text-white px-4 py-2 rounded-lg flex items-center justify-center gap-2 transition-colors"
                    >
                      <Key className="w-4 h-4" />
                      Generate API Key
                    </button>
                  </div>
                );
              })}
            </div>

            {filteredProducts.length === 0 && (
              <div className="bg-slate-800/50 border border-slate-700 rounded-xl p-12 text-center">
                <Package className="w-16 h-16 text-slate-600 mx-auto mb-4" />
                <h3 className="text-xl font-semibold text-white mb-2">No APIs Found</h3>
                <p className="text-slate-400">Try adjusting your search or filters</p>
              </div>
            )}
          </div>
        )}

        {activeTab === 'my-keys' && (
          <div className="space-y-6">
            <div className="flex items-center justify-between">
              <h2 className="text-2xl font-bold text-white">My API Keys</h2>
              <button
                onClick={() => setActiveTab('marketplace')}
                className="bg-emerald-600 hover:bg-emerald-700 text-white px-6 py-3 rounded-lg flex items-center gap-2"
              >
                <Plus className="w-5 h-5" />
                Generate New Key
              </button>
            </div>

            {myKeys.length === 0 ? (
              <div className="bg-slate-800/50 border border-slate-700 rounded-xl p-12 text-center">
                <Key className="w-16 h-16 text-slate-600 mx-auto mb-4" />
                <h3 className="text-xl font-semibold text-white mb-2">No API Keys Yet</h3>
                <p className="text-slate-400 mb-6">Generate your first API key to get started</p>
                <button
                  onClick={() => setActiveTab('marketplace')}
                  className="bg-emerald-600 hover:bg-emerald-700 text-white px-6 py-3 rounded-lg inline-flex items-center gap-2"
                >
                  <Plus className="w-5 h-5" />
                  Browse APIs
                </button>
              </div>
            ) : (
              <div className="space-y-4">
                {myKeys.map((keyData) => {
                  const product = apiProducts.find(p => p.id === keyData.product_id);
                  const industry = product ? getIndustryById(product.industry_id) : null;

                  return (
                    <div key={keyData.id} className="bg-slate-800/50 border border-slate-700 rounded-xl p-6">
                      <div className="flex items-start justify-between mb-4">
                        <div className="flex-1">
                          <div className="flex items-center gap-3 mb-2 flex-wrap">
                            <h3 className="text-xl font-semibold text-white">{keyData.key_name}</h3>
                            <span className={`px-3 py-1 rounded-full text-xs font-medium border ${
                              keyData.is_active
                                ? 'bg-green-500/20 text-green-400 border-green-500/30'
                                : 'bg-red-500/20 text-red-400 border-red-500/30'
                            }`}>
                              {keyData.is_active ? 'ACTIVE' : 'INACTIVE'}
                            </span>
                            <span className="px-3 py-1 rounded-full text-xs font-medium bg-blue-500/20 text-blue-400 border border-blue-500/30">
                              {keyData.environment.toUpperCase()}
                            </span>
                            {industry && (
                              <span className="px-3 py-1 rounded-full text-xs font-medium bg-emerald-500/20 text-emerald-400 border border-emerald-500/30">
                                {industry.industry_name}
                              </span>
                            )}
                          </div>
                          {product && <p className="text-slate-400 text-sm">{product.product_name}</p>}
                        </div>
                      </div>

                      <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
                        <div className="bg-slate-900/50 border border-slate-700 rounded-lg p-4">
                          <p className="text-slate-400 text-xs mb-1">Total Requests</p>
                          <p className="text-white text-lg font-semibold">{keyData.total_requests.toLocaleString()}</p>
                        </div>
                        <div className="bg-slate-900/50 border border-slate-700 rounded-lg p-4">
                          <p className="text-slate-400 text-xs mb-1">Created</p>
                          <p className="text-white text-sm font-semibold">{new Date(keyData.created_at).toLocaleDateString()}</p>
                        </div>
                        {product && (
                          <div className="bg-slate-900/50 border border-slate-700 rounded-lg p-4">
                            <p className="text-slate-400 text-xs mb-1">Rate Limit</p>
                            <p className="text-white text-sm font-semibold">{product.rate_limit_per_minute}/min</p>
                          </div>
                        )}
                      </div>

                      <div className="border-t border-slate-700 pt-4 space-y-4">
                        <div>
                          <div className="flex items-center justify-between mb-2">
                            <label className="text-sm font-medium text-slate-300 flex items-center gap-2">
                              <Key className="w-4 h-4" />
                              API Key
                            </label>
                            <button
                              onClick={() => copyToClipboard(keyData.api_key, `api_${keyData.id}`)}
                              className="text-emerald-400 hover:text-emerald-300 text-sm flex items-center gap-1"
                            >
                              {copiedKey === `api_${keyData.id}` ? (
                                <><CheckCircle className="w-4 h-4" /> Copied!</>
                              ) : (
                                <><Copy className="w-4 h-4" /> Copy</>
                              )}
                            </button>
                          </div>
                          <div className="bg-slate-900/50 border border-slate-700 rounded-lg px-4 py-3 font-mono text-sm text-white break-all">
                            {keyData.api_key}
                          </div>
                        </div>

                        {keyData.api_secret && (
                          <div>
                            <div className="flex items-center justify-between mb-2">
                              <label className="text-sm font-medium text-slate-300 flex items-center gap-2">
                                <Shield className="w-4 h-4" />
                                API Secret
                              </label>
                              <button
                                onClick={() => copyToClipboard(keyData.api_secret, `secret_${keyData.id}`)}
                                className="text-emerald-400 hover:text-emerald-300 text-sm flex items-center gap-1"
                              >
                                {copiedKey === `secret_${keyData.id}` ? (
                                  <><CheckCircle className="w-4 h-4" /> Copied!</>
                                ) : (
                                  <><Copy className="w-4 h-4" /> Copy</>
                                )}
                              </button>
                            </div>
                            <div className="bg-slate-900/50 border border-slate-700 rounded-lg px-4 py-3 font-mono text-sm text-white break-all">
                              {keyData.api_secret}
                            </div>
                          </div>
                        )}

                        {product && (
                          <div className="bg-slate-900/50 border border-slate-700 rounded-lg p-4">
                            <p className="text-slate-300 text-sm font-medium mb-2">API Endpoint:</p>
                            <code className="text-cyan-400 text-sm">{product.api_endpoint}</code>
                          </div>
                        )}
                      </div>
                    </div>
                  );
                })}
              </div>
            )}
          </div>
        )}
      </div>

      {generateKeyModal && (
        <div className="fixed inset-0 bg-black/60 backdrop-blur-sm flex items-center justify-center z-50 p-4">
          <div className="bg-slate-800 border border-slate-700 rounded-2xl p-6 max-w-2xl w-full">
            <div className="flex items-start justify-between mb-6">
              <div>
                <h3 className="text-2xl font-bold text-white mb-2">Generate API Key</h3>
                <p className="text-slate-400">{generateKeyModal.product_name}</p>
              </div>
              <button
                onClick={() => setGenerateKeyModal(null)}
                className="text-slate-400 hover:text-white"
              >
                <X className="w-6 h-6" />
              </button>
            </div>

            <div className="space-y-4 mb-6">
              <div>
                <label className="block text-sm font-medium text-slate-300 mb-2">
                  Key Name *
                </label>
                <input
                  type="text"
                  value={newKeyName}
                  onChange={(e) => setNewKeyName(e.target.value)}
                  placeholder="e.g., Production API Key"
                  className="w-full bg-slate-900 border border-slate-700 rounded-lg px-4 py-3 text-white placeholder-slate-500 focus:outline-none focus:ring-2 focus:ring-emerald-500"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-slate-300 mb-2">
                  Environment
                </label>
                <div className="grid grid-cols-2 gap-3">
                  <button
                    onClick={() => setSelectedEnvironment('production')}
                    className={`p-4 rounded-lg border-2 transition-all ${
                      selectedEnvironment === 'production'
                        ? 'border-emerald-500 bg-emerald-500/10'
                        : 'border-slate-700 bg-slate-900/50 hover:border-slate-600'
                    }`}
                  >
                    <p className="text-white font-semibold mb-1">Production</p>
                    <p className="text-slate-400 text-xs">Live environment</p>
                  </button>
                  <button
                    onClick={() => setSelectedEnvironment('sandbox')}
                    className={`p-4 rounded-lg border-2 transition-all ${
                      selectedEnvironment === 'sandbox'
                        ? 'border-emerald-500 bg-emerald-500/10'
                        : 'border-slate-700 bg-slate-900/50 hover:border-slate-600'
                    }`}
                  >
                    <p className="text-white font-semibold mb-1">Sandbox</p>
                    <p className="text-slate-400 text-xs">Testing environment</p>
                  </button>
                </div>
              </div>

              <div className="bg-slate-900/50 border border-slate-700 rounded-lg p-4">
                <h4 className="text-white font-semibold mb-3">API Details</h4>
                <div className="space-y-2 text-sm">
                  <div className="flex justify-between">
                    <span className="text-slate-400">Pricing Model:</span>
                    <span className="text-white capitalize">{generateKeyModal.pricing_model.replace('_', ' ')}</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-slate-400">Rate Limit:</span>
                    <span className="text-white">{generateKeyModal.rate_limit_per_minute}/min</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-slate-400">Daily Limit:</span>
                    <span className="text-white">{generateKeyModal.rate_limit_per_day.toLocaleString()}/day</span>
                  </div>
                </div>
              </div>
            </div>

            <div className="flex gap-3">
              <button
                onClick={() => setGenerateKeyModal(null)}
                disabled={generating}
                className="flex-1 bg-slate-700 hover:bg-slate-600 text-white px-6 py-3 rounded-lg transition-colors disabled:opacity-50"
              >
                Cancel
              </button>
              <button
                onClick={handleGenerateKey}
                disabled={generating || !newKeyName.trim()}
                className="flex-1 bg-emerald-600 hover:bg-emerald-700 text-white px-6 py-3 rounded-lg transition-colors disabled:opacity-50 flex items-center justify-center gap-2"
              >
                {generating ? (
                  <>
                    <div className="w-5 h-5 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                    Generating...
                  </>
                ) : (
                  <>
                    <Key className="w-5 h-5" />
                    Generate Key
                  </>
                )}
              </button>
            </div>
          </div>
        </div>
      )}

      {notification && (
        <div className="fixed top-4 right-4 z-50 animate-slide-in">
          <div className={`${
            notification.type === 'success' ? 'bg-green-500/20 border-green-500/50' : 'bg-red-500/20 border-red-500/50'
          } border backdrop-blur-sm rounded-lg p-4 max-w-md`}>
            <div className="flex items-start gap-3">
              <div className={`${
                notification.type === 'success' ? 'bg-green-500' : 'bg-red-500'
              } rounded-full p-1`}>
                {notification.type === 'success' ? (
                  <CheckCircle className="w-5 h-5 text-white" />
                ) : (
                  <X className="w-5 h-5 text-white" />
                )}
              </div>
              <div className="flex-1">
                <p className={`font-semibold ${notification.type === 'success' ? 'text-green-400' : 'text-red-400'}`}>
                  {notification.type === 'success' ? 'Success!' : 'Error'}
                </p>
                <p className="text-white text-sm mt-1">{notification.message}</p>
              </div>
              <button
                onClick={() => setNotification(null)}
                className="text-slate-400 hover:text-white"
              >
                <X className="w-5 h-5" />
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
