import { useState, useEffect } from 'react';
import { db } from '../../lib/db';
import { Plug, Plus, CheckCircle, XCircle, Settings, Trash2, RefreshCw } from 'lucide-react';

type Integration = {
  id: string;
  name: string;
  type: string;
  status: 'active' | 'inactive' | 'error';
  is_active: boolean;
  last_synced_at: string | null;
  created_at: string;
};

const availableIntegrations = [
  { name: 'Twilio', type: 'sms_provider', description: 'SMS and voice communications', icon: '' },
  { name: 'SendGrid', type: 'email_provider', description: 'Email delivery service', icon: '' },
  { name: 'Stripe', type: 'payment_gateway', description: 'Payment processing', icon: '' },
  { name: 'Salesforce', type: 'crm', description: 'Customer relationship management', icon: '' },
  { name: 'HubSpot', type: 'crm', description: 'Marketing and sales CRM', icon: '' },
  { name: 'Google Analytics', type: 'analytics', description: 'Web analytics platform', icon: '' },
  { name: 'Slack', type: 'webhook', description: 'Team communication', icon: '' },
  { name: 'Zapier', type: 'webhook', description: 'Automation platform', icon: '' },
];

export default function IntegrationsManagement() {
  const [integrations, setIntegrations] = useState<Integration[]>([]);
  const [loading, setLoading] = useState(true);
  const [showAddModal, setShowAddModal] = useState(false);
  const [showConfigModal, setShowConfigModal] = useState(false);
  const [selectedIntegration, setSelectedIntegration] = useState<{
    name: string;
    type: string;
  } | null>(null);
  const [configData, setConfigData] = useState({
    api_key: '',
    api_secret: '',
    endpoint: ''
  });
  const [notification, setNotification] = useState<{
    type: 'success' | 'error';
    message: string;
  } | null>(null);

  useEffect(() => {
    fetchIntegrations();
  }, []);

  useEffect(() => {
    if (notification) {
      const timer = setTimeout(() => setNotification(null), 5000);
      return () => clearTimeout(timer);
    }
  }, [notification]);

  const handleConnectIntegration = (integration: { name: string; type: string }) => {
    setSelectedIntegration(integration);
    setShowConfigModal(true);
    setShowAddModal(false);
  };

  const handleSaveIntegration = async () => {
    if (!selectedIntegration) return;

    try {
      const { data: { user } } = await db.auth.getUser();
      if (!user) throw new Error('Not authenticated');

      const { error } = await db.from('integrations').insert({
        user_id: user.id,
        name: selectedIntegration.name,
        type: selectedIntegration.type,
        config: configData,
        status: 'active',
        is_active: true
      });

      if (error) throw error;

      setNotification({
        type: 'success',
        message: `${selectedIntegration.name} connected successfully`
      });

      setShowConfigModal(false);
      setSelectedIntegration(null);
      setConfigData({
        api_key: '',
        api_secret: '',
        endpoint: ''
      });
      fetchIntegrations();
    } catch (error: any) {
      console.error('Error connecting integration:', error);
      setNotification({
        type: 'error',
        message: error.message || 'Failed to connect integration'
      });
    }
  };

  const fetchIntegrations = async () => {
    setLoading(true);
    const { data, error } = await db
      .from('integrations')
      .select('*')
      .order('created_at', { ascending: false });

    if (error) {
      console.error('Error fetching integrations:', error);
    } else {
      setIntegrations(data || []);
    }
    setLoading(false);
  };

  const toggleIntegration = async (id: string, currentStatus: boolean) => {
    const { error } = await db
      .from('integrations')
      .update({ is_active: !currentStatus })
      .eq('id', id);

    if (!error) {
      fetchIntegrations();
    }
  };

  const deleteIntegration = async (id: string) => {
    if (!confirm('Are you sure you want to remove this integration?')) return;

    const { error } = await db
      .from('integrations')
      .delete()
      .eq('id', id);

    if (!error) {
      fetchIntegrations();
    }
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'active': return 'bg-emerald-500/20 text-emerald-400 border-emerald-500/50';
      case 'inactive': return 'bg-slate-500/20 text-slate-400 border-slate-500/50';
      case 'error': return 'bg-red-500/20 text-red-400 border-red-500/50';
      default: return 'bg-slate-500/20 text-slate-400 border-slate-500/50';
    }
  };

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-2xl font-bold text-white mb-2">Integrations Management</h2>
          <p className="text-slate-400">Connect third-party services and manage integrations</p>
        </div>
        <button
          onClick={() => setShowAddModal(true)}
          className="flex items-center gap-2 bg-gradient-to-r from-cyan-500 to-blue-500 hover:from-cyan-600 hover:to-blue-600 text-white px-4 py-2 rounded-lg font-semibold transition-all"
        >
          <Plus className="w-4 h-4" />
          Add Integration
        </button>
      </div>

      <div className="grid grid-cols-4 gap-6">
        <div className="bg-gradient-to-br from-slate-800/50 to-slate-800/30 backdrop-blur-sm border border-slate-700/50 rounded-xl p-6">
          <div className="flex items-center gap-3 mb-4">
            <div className="bg-cyan-500/20 p-3 rounded-lg">
              <Plug className="w-5 h-5 text-cyan-400" />
            </div>
            <div>
              <p className="text-slate-400 text-sm">Total</p>
              <p className="text-2xl font-bold text-white">{integrations.length}</p>
            </div>
          </div>
          <p className="text-slate-500 text-sm">Integrations</p>
        </div>

        <div className="bg-gradient-to-br from-slate-800/50 to-slate-800/30 backdrop-blur-sm border border-slate-700/50 rounded-xl p-6">
          <div className="flex items-center gap-3 mb-4">
            <div className="bg-emerald-500/20 p-3 rounded-lg">
              <CheckCircle className="w-5 h-5 text-emerald-400" />
            </div>
            <div>
              <p className="text-slate-400 text-sm">Active</p>
              <p className="text-2xl font-bold text-white">{integrations.filter(i => i.is_active).length}</p>
            </div>
          </div>
          <p className="text-emerald-400 text-sm">Connected</p>
        </div>

        <div className="bg-gradient-to-br from-slate-800/50 to-slate-800/30 backdrop-blur-sm border border-slate-700/50 rounded-xl p-6">
          <div className="flex items-center gap-3 mb-4">
            <div className="bg-slate-500/20 p-3 rounded-lg">
              <XCircle className="w-5 h-5 text-slate-400" />
            </div>
            <div>
              <p className="text-slate-400 text-sm">Inactive</p>
              <p className="text-2xl font-bold text-white">{integrations.filter(i => !i.is_active).length}</p>
            </div>
          </div>
          <p className="text-slate-500 text-sm">Disconnected</p>
        </div>

        <div className="bg-gradient-to-br from-slate-800/50 to-slate-800/30 backdrop-blur-sm border border-slate-700/50 rounded-xl p-6">
          <div className="flex items-center gap-3 mb-4">
            <div className="bg-red-500/20 p-3 rounded-lg">
              <XCircle className="w-5 h-5 text-red-400" />
            </div>
            <div>
              <p className="text-slate-400 text-sm">Errors</p>
              <p className="text-2xl font-bold text-white">{integrations.filter(i => i.status === 'error').length}</p>
            </div>
          </div>
          <p className="text-red-400 text-sm">Need attention</p>
        </div>
      </div>

      {showAddModal && (
        <div className="bg-gradient-to-br from-slate-800/50 to-slate-800/30 backdrop-blur-sm border border-slate-700/50 rounded-xl p-6">
          <h3 className="text-xl font-bold text-white mb-6">Available Integrations</h3>
          <div className="grid grid-cols-2 gap-4">
            {availableIntegrations.map((integration) => (
              <div
                key={integration.name}
                className="bg-slate-900/50 border border-slate-700 hover:border-cyan-500/50 rounded-lg p-4 transition-all group"
              >
                <div className="flex items-start gap-4">
                  <div className="text-4xl">{integration.icon}</div>
                  <div className="flex-1">
                    <h4 className="text-white font-semibold mb-1">{integration.name}</h4>
                    <p className="text-slate-400 text-sm mb-3">{integration.description}</p>
                    <button
                      onClick={() => handleConnectIntegration(integration)}
                      className="text-cyan-400 hover:text-cyan-300 text-sm font-medium"
                    >
                      Connect 
                    </button>
                  </div>
                </div>
              </div>
            ))}
          </div>
          <div className="mt-6 flex justify-end">
            <button
              onClick={() => setShowAddModal(false)}
              className="px-4 py-2 bg-slate-800 hover:bg-slate-700 text-slate-300 rounded-lg font-medium transition-all"
            >
              Close
            </button>
          </div>
        </div>
      )}

      <div className="bg-gradient-to-br from-slate-800/50 to-slate-800/30 backdrop-blur-sm border border-slate-700/50 rounded-xl p-6">
        <h3 className="text-xl font-bold text-white mb-6">Connected Integrations</h3>

        {loading ? (
          <div className="text-center py-12">
            <div className="w-8 h-8 border-4 border-cyan-500 border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
            <p className="text-slate-400">Loading integrations...</p>
          </div>
        ) : integrations.length === 0 ? (
          <div className="text-center py-12">
            <Plug className="w-12 h-12 text-slate-600 mx-auto mb-4" />
            <p className="text-slate-400 mb-2">No integrations connected yet</p>
            <p className="text-slate-500 text-sm">Connect your first integration to get started</p>
          </div>
        ) : (
          <div className="space-y-3">
            {integrations.map((integration) => (
              <div key={integration.id} className="bg-slate-900/50 border border-slate-700 rounded-lg p-4">
                <div className="flex items-center justify-between">
                  <div className="flex items-center gap-4">
                    <div className="bg-gradient-to-r from-cyan-500 to-blue-500 p-3 rounded-lg">
                      <Plug className="w-6 h-6 text-white" />
                    </div>
                    <div>
                      <div className="flex items-center gap-3 mb-1">
                        <h4 className="text-white font-semibold">{integration.name}</h4>
                        <span className={`inline-flex items-center gap-1 px-2 py-1 rounded text-xs font-semibold border ${getStatusColor(integration.status)}`}>
                          {integration.status === 'active' && <CheckCircle className="w-3 h-3" />}
                          {integration.status === 'error' && <XCircle className="w-3 h-3" />}
                          {integration.status.toUpperCase()}
                        </span>
                      </div>
                      <div className="flex items-center gap-4 text-sm text-slate-400">
                        <span className="capitalize">{integration.type.replace('_', ' ')}</span>
                        {integration.last_synced_at && (
                          <span>Last synced: {new Date(integration.last_synced_at).toLocaleString()}</span>
                        )}
                      </div>
                    </div>
                  </div>
                  <div className="flex items-center gap-2">
                    <button className="p-2 hover:bg-slate-700 rounded-lg transition-colors group">
                      <RefreshCw className="w-4 h-4 text-slate-400 group-hover:text-cyan-400" />
                    </button>
                    <button className="p-2 hover:bg-slate-700 rounded-lg transition-colors group">
                      <Settings className="w-4 h-4 text-slate-400 group-hover:text-blue-400" />
                    </button>
                    <button
                      onClick={() => toggleIntegration(integration.id, integration.is_active)}
                      className={`px-3 py-1 rounded-lg text-xs font-semibold transition-all ${
                        integration.is_active
                          ? 'bg-emerald-500/20 text-emerald-400 hover:bg-emerald-500/30'
                          : 'bg-slate-700 text-slate-400 hover:bg-slate-600'
                      }`}
                    >
                      {integration.is_active ? 'Enabled' : 'Disabled'}
                    </button>
                    <button
                      onClick={() => deleteIntegration(integration.id)}
                      className="p-2 hover:bg-slate-700 rounded-lg transition-colors group"
                    >
                      <Trash2 className="w-4 h-4 text-slate-400 group-hover:text-red-400" />
                    </button>
                  </div>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>

      {showConfigModal && selectedIntegration && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
          <div className="bg-slate-900 border border-slate-700 rounded-xl p-6 max-w-md w-full">
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-2xl font-bold text-white">Configure {selectedIntegration.name}</h2>
              <button
                onClick={() => {
                  setShowConfigModal(false);
                  setSelectedIntegration(null);
                  setConfigData({
                    api_key: '',
                    api_secret: '',
                    endpoint: ''
                  });
                }}
                className="text-slate-400 hover:text-white"
              >
                <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            </div>

            <div className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-slate-300 mb-2">API Key</label>
                <input
                  type="text"
                  value={configData.api_key}
                  onChange={(e) => setConfigData({ ...configData, api_key: e.target.value })}
                  placeholder="Enter API key"
                  className="w-full bg-slate-800 border border-slate-700 rounded-lg px-4 py-2 text-white focus:outline-none focus:border-cyan-500"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-slate-300 mb-2">API Secret</label>
                <input
                  type="password"
                  value={configData.api_secret}
                  onChange={(e) => setConfigData({ ...configData, api_secret: e.target.value })}
                  placeholder="Enter API secret"
                  className="w-full bg-slate-800 border border-slate-700 rounded-lg px-4 py-2 text-white focus:outline-none focus:border-cyan-500"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-slate-300 mb-2">Endpoint (Optional)</label>
                <input
                  type="text"
                  value={configData.endpoint}
                  onChange={(e) => setConfigData({ ...configData, endpoint: e.target.value })}
                  placeholder="https://api.example.com"
                  className="w-full bg-slate-800 border border-slate-700 rounded-lg px-4 py-2 text-white focus:outline-none focus:border-cyan-500"
                />
              </div>

              <div className="flex gap-3 pt-4">
                <button
                  type="button"
                  onClick={() => {
                    setShowConfigModal(false);
                    setSelectedIntegration(null);
                    setConfigData({
                      api_key: '',
                      api_secret: '',
                      endpoint: ''
                    });
                  }}
                  className="flex-1 bg-slate-800 hover:bg-slate-700 text-white px-4 py-2 rounded-lg transition-colors"
                >
                  Cancel
                </button>
                <button
                  onClick={handleSaveIntegration}
                  className="flex-1 bg-gradient-to-r from-cyan-500 to-blue-500 hover:from-cyan-600 hover:to-blue-600 text-white px-4 py-2 rounded-lg font-semibold transition-all"
                >
                  Connect
                </button>
              </div>
            </div>
          </div>
        </div>
      )}

      {notification && (
        <div className="fixed top-4 right-4 z-50">
          <div className={`${
            notification.type === 'success' ? 'bg-green-500/20 border-green-500/50' : 'bg-red-500/20 border-red-500/50'
          } border backdrop-blur-sm rounded-lg p-4 max-w-md`}>
            <div className="flex items-start gap-3">
              <div className={`${
                notification.type === 'success' ? 'bg-green-500' : 'bg-red-500'
              } rounded-full p-1`}>
                {notification.type === 'success' ? (
                  <svg className="w-5 h-5 text-white" fill="none" strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" viewBox="0 0 24 24" stroke="currentColor">
                    <path d="M5 13l4 4L19 7"></path>
                  </svg>
                ) : (
                  <svg className="w-5 h-5 text-white" fill="none" strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" viewBox="0 0 24 24" stroke="currentColor">
                    <path d="M6 18L18 6M6 6l12 12"></path>
                  </svg>
                )}
              </div>
              <div className="flex-1">
                <p className={`font-semibold ${notification.type === 'success' ? 'text-green-400' : 'text-red-400'}`}>
                  {notification.type === 'success' ? 'Success!' : 'Error'}
                </p>
                <p className="text-white text-sm mt-1">{notification.message}</p>
              </div>
              <button
                onClick={() => setNotification(null)}
                className="text-slate-400 hover:text-white"
              >
                <svg className="w-5 h-5" fill="none" strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" viewBox="0 0 24 24" stroke="currentColor">
                  <path d="M6 18L18 6M6 6l12 12"></path>
                </svg>
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
