import React, { useState, useEffect } from 'react';
import { db } from '../../lib/db';
import { useAuth } from '../../contexts/AuthContext';
import {
  Plus, Play, Pause, Trash2, Edit, Search, BarChart3,
  Calendar, TrendingUp, Linkedin, X, Users, Target, Briefcase,
  Share2, MessageCircle, ThumbsUp
} from 'lucide-react';

export default function LinkedInCampaigns() {
  const { user } = useAuth();
  const [campaigns, setCampaigns] = useState<any[]>([]);
  const [linkedinCampaigns, setLinkedinCampaigns] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [selectedCampaign, setSelectedCampaign] = useState<any>(null);
  const [filterStatus, setFilterStatus] = useState<string>('all');
  const [searchQuery, setSearchQuery] = useState('');
  const [notification, setNotification] = useState<{ type: 'success' | 'error'; message: string } | null>(null);
  const [formData, setFormData] = useState({
    name: '',
    description: '',
    budget: '',
    objective: 'brand_awareness',
    sponsoredContent: false,
    textAds: false,
    messageAds: false,
    dynamicAds: false
  });

  useEffect(() => {
    if (user) {
      loadCampaigns();
    }
  }, [user]);

  useEffect(() => {
    if (notification) {
      const timer = setTimeout(() => setNotification(null), 5000);
      return () => clearTimeout(timer);
    }
  }, [notification]);

  const loadCampaigns = async () => {
    try {
      const { data: campaignData, error: campaignError } = await db
        .from('campaigns')
        .select('*')
        .eq('channel_type', 'linkedin')
        .order('created_at', { ascending: false });

      if (campaignError) throw campaignError;

      const { data: linkedinData, error: linkedinError } = await db
        .from('linkedin_campaigns')
        .select('*')
        .order('created_at', { ascending: false });

      if (linkedinError) throw linkedinError;

      const mergedData = (campaignData || []).map((camp: any) => {
        const linkedinDetails = (linkedinData || []).find((li: any) => li.campaign_id === camp.id);
        return { ...camp, linkedin_details: linkedinDetails };
      });

      setCampaigns(mergedData);
      setLinkedinCampaigns(linkedinData || []);
    } catch (error) {
      console.error('Error loading campaigns:', error);
      setNotification({ type: 'error', message: 'Failed to load campaigns' });
    } finally {
      setLoading(false);
    }
  };

  const updateCampaignStatus = async (campaignId: string, newStatus: string) => {
    try {
      const updateData: any = { status: newStatus };

      if (newStatus === 'running') {
        const campaign = campaigns.find(c => c.id === campaignId);
        if (!campaign?.started_at) {
          updateData.started_at = new Date().toISOString();
        }
      }

      if (newStatus === 'completed') {
        updateData.completed_at = new Date().toISOString();
      }

      const { error } = await db
        .from('campaigns')
        .update(updateData)
        .eq('id', campaignId);

      if (error) throw error;

      setNotification({
        type: 'success',
        message: `Campaign ${newStatus === 'running' ? 'started' : newStatus} successfully`
      });

      await loadCampaigns();
    } catch (error) {
      console.error('Error updating campaign:', error);
      setNotification({ type: 'error', message: 'Failed to update campaign status' });
    }
  };

  const deleteCampaign = async (campaignId: string) => {
    if (!confirm('Are you sure you want to delete this campaign?')) return;

    try {
      const { error } = await db
        .from('campaigns')
        .delete()
        .eq('id', campaignId);

      if (error) throw error;

      setNotification({ type: 'success', message: 'Campaign deleted successfully' });
      await loadCampaigns();
    } catch (error) {
      console.error('Error deleting campaign:', error);
      setNotification({ type: 'error', message: 'Failed to delete campaign' });
    }
  };

  const createCampaign = async () => {
    if (!formData.name.trim()) {
      setNotification({ type: 'error', message: 'Campaign name is required' });
      return;
    }

    try {
      const { data: campaign, error: campaignError } = await db
        .from('campaigns')
        .insert({
          name: formData.name,
          description: formData.description,
          channel_type: 'linkedin',
          status: 'draft'
        })
        .select()
        .single();

      if (campaignError) throw campaignError;

      const { error: linkedinError } = await db
        .from('linkedin_campaigns')
        .insert({
          campaign_id: campaign.id,
          campaign_name: formData.name,
          objective: formData.objective,
          sponsored_content: formData.sponsoredContent,
          text_ads: formData.textAds,
          message_ads: formData.messageAds,
          dynamic_ads: formData.dynamicAds,
          total_budget: parseFloat(formData.budget) || 0,
          status: 'draft'
        });

      if (linkedinError) throw linkedinError;

      setNotification({ type: 'success', message: 'Campaign created successfully' });
      setShowCreateModal(false);
      setFormData({
        name: '',
        description: '',
        budget: '',
        objective: 'brand_awareness',
        sponsoredContent: false,
        textAds: false,
        messageAds: false,
        dynamicAds: false
      });
      await loadCampaigns();
    } catch (error) {
      console.error('Error creating campaign:', error);
      setNotification({ type: 'error', message: 'Failed to create campaign' });
    }
  };

  const filteredCampaigns = campaigns.filter(campaign => {
    const matchesStatus = filterStatus === 'all' || campaign.status === filterStatus;
    const matchesSearch = campaign.name?.toLowerCase().includes(searchQuery.toLowerCase());
    return matchesStatus && matchesSearch;
  });

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'running': return 'bg-green-500/20 text-green-400 border-green-500/30';
      case 'paused': return 'bg-yellow-500/20 text-yellow-400 border-yellow-500/30';
      case 'completed': return 'bg-blue-500/20 text-blue-400 border-blue-500/30';
      case 'draft': return 'bg-slate-500/20 text-slate-400 border-slate-500/30';
      case 'scheduled': return 'bg-purple-500/20 text-purple-400 border-purple-500/30';
      default: return 'bg-slate-500/20 text-slate-400 border-slate-500/30';
    }
  };

  const totalSent = campaigns.reduce((sum, c) => sum + (c.messages_sent || 0), 0);
  const totalDelivered = campaigns.reduce((sum, c) => sum + (c.messages_delivered || 0), 0);
  const totalCost = campaigns.reduce((sum, c) => sum + (parseFloat(c.linkedin_details?.total_budget) || 0), 0);
  const activeCampaigns = campaigns.filter(c => c.status === 'running').length;
  const deliveryRate = totalSent > 0 ? ((totalDelivered / totalSent) * 100).toFixed(1) : 0;

  const totalLeads = linkedinCampaigns.reduce((sum, c) => sum + (c.leads_generated || 0), 0);

  if (loading) {
    return (
      <div className="flex items-center justify-center h-screen bg-slate-950">
        <div className="text-slate-400">Loading...</div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-slate-950 p-8">
      {notification && (
        <div className={`fixed top-4 right-4 z-50 px-6 py-3 rounded-lg border ${
          notification.type === 'success'
            ? 'bg-green-500/20 border-green-500/50 text-green-400'
            : 'bg-red-500/20 border-red-500/50 text-red-400'
        }`}>
          {notification.message}
        </div>
      )}

      <div className="mb-8">
        <h1 className="text-4xl font-bold text-white mb-2 flex items-center gap-3">
          <Linkedin className="w-10 h-10 text-blue-600" />
          LinkedIn
        </h1>
        <p className="text-slate-400">Manage and monitor your LinkedIn B2B campaigns</p>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-5 gap-6 mb-8">
        <div className="bg-gradient-to-br from-blue-600/10 to-blue-700/10 border border-blue-600/30 rounded-xl p-6">
          <div className="flex items-center justify-between mb-3">
            <div className="p-3 bg-blue-600/20 rounded-lg">
              <Briefcase className="w-6 h-6 text-blue-500" />
            </div>
            <TrendingUp className="w-5 h-5 text-blue-500" />
          </div>
          <div className="text-3xl font-bold text-white mb-1">{totalSent.toLocaleString()}</div>
          <div className="text-slate-400 text-sm">Messages Sent</div>
        </div>

        <div className="bg-slate-800/50 border border-slate-700 rounded-xl p-6">
          <div className="flex items-center justify-between mb-3">
            <div className="p-3 bg-green-500/20 rounded-lg">
              <Target className="w-6 h-6 text-green-400" />
            </div>
            <span className={`text-xs font-semibold px-2 py-1 rounded ${Number(deliveryRate) >= 95 ? 'bg-green-500/20 text-green-400' : 'bg-orange-500/20 text-orange-400'}`}>
              {deliveryRate}%
            </span>
          </div>
          <div className="text-3xl font-bold text-white mb-1">{deliveryRate}%</div>
          <div className="text-slate-400 text-sm">Delivery Rate</div>
        </div>

        <div className="bg-slate-800/50 border border-slate-700 rounded-xl p-6">
          <div className="flex items-center justify-between mb-3">
            <div className="p-3 bg-purple-500/20 rounded-lg">
              <BarChart3 className="w-6 h-6 text-purple-400" />
            </div>
            <span className="text-purple-400 text-sm font-semibold">LIVE</span>
          </div>
          <div className="text-3xl font-bold text-white mb-1">{activeCampaigns}</div>
          <div className="text-slate-400 text-sm">Active Campaigns</div>
        </div>

        <div className="bg-slate-800/50 border border-slate-700 rounded-xl p-6">
          <div className="flex items-center justify-between mb-3">
            <div className="p-3 bg-cyan-500/20 rounded-lg">
              <Users className="w-6 h-6 text-cyan-400" />
            </div>
          </div>
          <div className="text-3xl font-bold text-white mb-1">{totalLeads.toLocaleString()}</div>
          <div className="text-slate-400 text-sm">Leads Generated</div>
        </div>

        <div className="bg-slate-800/50 border border-slate-700 rounded-xl p-6">
          <div className="flex items-center justify-between mb-3">
            <div className="p-3 bg-orange-500/20 rounded-lg">
              <TrendingUp className="w-6 h-6 text-orange-400" />
            </div>
          </div>
          <div className="text-3xl font-bold text-white mb-1">RM {totalCost.toFixed(2)}</div>
          <div className="text-slate-400 text-sm">Total Cost</div>
        </div>
      </div>

      <div className="bg-slate-800/50 border border-slate-700 rounded-xl p-6 mb-6">
        <div className="flex items-center justify-between mb-6">
          <h2 className="text-2xl font-bold text-white">Campaigns</h2>
          <button
            onClick={() => setShowCreateModal(true)}
            className="px-6 py-3 bg-gradient-to-r from-blue-600 to-blue-700 hover:from-blue-700 hover:to-blue-800 text-white rounded-lg transition-all flex items-center gap-2 font-medium"
          >
            <Plus className="w-5 h-5" />
            New Campaign
          </button>
        </div>

        <div className="flex gap-4 mb-6">
          <div className="flex-1 relative">
            <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-slate-400" />
            <input
              type="text"
              placeholder="Search campaigns..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="w-full pl-10 pr-4 py-2 bg-slate-900/50 border border-slate-700 rounded-lg text-white focus:outline-none focus:border-blue-600 transition-colors"
            />
          </div>
          <select
            value={filterStatus}
            onChange={(e) => setFilterStatus(e.target.value)}
            className="px-4 py-2 bg-slate-900/50 border border-slate-700 rounded-lg text-white focus:outline-none focus:border-blue-600 transition-colors"
          >
            <option value="all">All Status</option>
            <option value="draft">Draft</option>
            <option value="running">Running</option>
            <option value="paused">Paused</option>
            <option value="completed">Completed</option>
            <option value="scheduled">Scheduled</option>
          </select>
        </div>

        <div className="overflow-x-auto">
          <table className="w-full">
            <thead>
              <tr className="border-b border-slate-700">
                <th className="text-left p-4 text-slate-400 font-medium">Campaign Name</th>
                <th className="text-left p-4 text-slate-400 font-medium">Status</th>
                <th className="text-right p-4 text-slate-400 font-medium">Sent</th>
                <th className="text-right p-4 text-slate-400 font-medium">Delivered</th>
                <th className="text-right p-4 text-slate-400 font-medium">Failed</th>
                <th className="text-right p-4 text-slate-400 font-medium">Cost</th>
                <th className="text-left p-4 text-slate-400 font-medium">Date</th>
                <th className="text-right p-4 text-slate-400 font-medium">Actions</th>
              </tr>
            </thead>
            <tbody>
              {filteredCampaigns.length === 0 ? (
                <tr>
                  <td colSpan={8} className="p-12 text-center">
                    <Linkedin className="w-16 h-16 text-slate-600 mx-auto mb-4" />
                    <p className="text-slate-400 text-lg mb-2">No LinkedIn campaigns found</p>
                    <p className="text-slate-500 text-sm">Create your first LinkedIn campaign to get started</p>
                  </td>
                </tr>
              ) : (
                filteredCampaigns.map((campaign) => (
                  <tr key={campaign.id} className="border-b border-slate-700/50 hover:bg-slate-800/30 transition-colors">
                    <td className="p-4">
                      <div className="flex items-center gap-3">
                        <div className="p-2 bg-blue-600/20 rounded-lg">
                          <Linkedin className="w-5 h-5 text-blue-500" />
                        </div>
                        <div>
                          <div className="text-white font-medium">{campaign.name}</div>
                          <div className="text-slate-400 text-sm">{campaign.description || 'No description'}</div>
                        </div>
                      </div>
                    </td>
                    <td className="p-4">
                      <span className={`px-3 py-1 rounded-full text-xs font-medium border ${getStatusColor(campaign.status)}`}>
                        {campaign.status}
                      </span>
                    </td>
                    <td className="p-4 text-right text-white">{(campaign.messages_sent || 0).toLocaleString()}</td>
                    <td className="p-4 text-right text-green-400 font-medium">{(campaign.messages_delivered || 0).toLocaleString()}</td>
                    <td className="p-4 text-right text-red-400">{(campaign.messages_failed || 0).toLocaleString()}</td>
                    <td className="p-4 text-right text-white">RM {(parseFloat(campaign.linkedin_details?.total_budget) || 0).toFixed(2)}</td>
                    <td className="p-4 text-slate-400">
                      <div className="flex items-center gap-2">
                        <Calendar className="w-4 h-4" />
                        {new Date(campaign.created_at).toLocaleDateString('en-US', { month: 'numeric', day: 'numeric', year: 'numeric' })}
                      </div>
                    </td>
                    <td className="p-4">
                      <div className="flex items-center justify-end gap-2">
                        {campaign.status === 'running' ? (
                          <button
                            onClick={() => updateCampaignStatus(campaign.id, 'paused')}
                            className="p-2 hover:bg-yellow-500/20 text-yellow-400 rounded-lg transition-colors"
                            title="Pause"
                          >
                            <Pause className="w-4 h-4" />
                          </button>
                        ) : (campaign.status === 'paused' || campaign.status === 'draft') ? (
                          <button
                            onClick={() => updateCampaignStatus(campaign.id, 'running')}
                            className="p-2 hover:bg-green-500/20 text-green-400 rounded-lg transition-colors"
                            title="Start"
                          >
                            <Play className="w-4 h-4" />
                          </button>
                        ) : null}
                        <button
                          onClick={() => { setSelectedCampaign(campaign); setShowCreateModal(true); }}
                          className="p-2 hover:bg-blue-500/20 text-blue-400 rounded-lg transition-colors"
                          title="Edit"
                        >
                          <Edit className="w-4 h-4" />
                        </button>
                        <button
                          onClick={() => deleteCampaign(campaign.id)}
                          className="p-2 hover:bg-red-500/20 text-red-400 rounded-lg transition-colors"
                          title="Delete"
                        >
                          <Trash2 className="w-4 h-4" />
                        </button>
                      </div>
                    </td>
                  </tr>
                ))
              )}
            </tbody>
          </table>
        </div>
      </div>

      {showCreateModal && (
        <div className="fixed inset-0 bg-black/70 flex items-center justify-center z-50 p-4">
          <div className="bg-slate-800 rounded-xl border border-slate-700 max-w-2xl w-full max-h-[90vh] overflow-y-auto">
            <div className="p-6 border-b border-slate-700 flex items-center justify-between sticky top-0 bg-slate-800 z-10">
              <h3 className="text-2xl font-bold text-white flex items-center gap-3">
                <Linkedin className="w-7 h-7 text-blue-600" />
                {selectedCampaign ? 'Edit Campaign' : 'Create LinkedIn Campaign'}
              </h3>
              <button
                onClick={() => { setShowCreateModal(false); setSelectedCampaign(null); }}
                className="p-2 hover:bg-slate-700 rounded-lg transition-colors"
              >
                <X className="w-5 h-5 text-slate-400" />
              </button>
            </div>

            <div className="p-6 space-y-6">
              <div>
                <label className="block text-sm font-medium text-slate-300 mb-2">Campaign Name *</label>
                <input
                  type="text"
                  value={formData.name}
                  onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                  placeholder="Enter campaign name"
                  className="w-full bg-slate-900/50 border border-slate-700 rounded-lg px-4 py-3 text-white focus:outline-none focus:border-blue-600 transition-colors"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-slate-300 mb-2">Description</label>
                <textarea
                  value={formData.description}
                  onChange={(e) => setFormData({ ...formData, description: e.target.value })}
                  placeholder="Enter campaign description"
                  rows={3}
                  className="w-full bg-slate-900/50 border border-slate-700 rounded-lg px-4 py-3 text-white focus:outline-none focus:border-blue-600 transition-colors resize-none"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-slate-300 mb-2">Total Budget (RM)</label>
                <input
                  type="number"
                  value={formData.budget}
                  onChange={(e) => setFormData({ ...formData, budget: e.target.value })}
                  placeholder="0.00"
                  className="w-full bg-slate-900/50 border border-slate-700 rounded-lg px-4 py-3 text-white focus:outline-none focus:border-blue-600 transition-colors"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-slate-300 mb-2">Campaign Objective</label>
                <select
                  value={formData.objective}
                  onChange={(e) => setFormData({ ...formData, objective: e.target.value })}
                  className="w-full bg-slate-900/50 border border-slate-700 rounded-lg px-4 py-3 text-white focus:outline-none focus:border-blue-600 transition-colors"
                >
                  <option value="brand_awareness">Brand Awareness</option>
                  <option value="website_visits">Website Visits</option>
                  <option value="engagement">Engagement</option>
                  <option value="lead_generation">Lead Generation</option>
                  <option value="website_conversions">Website Conversions</option>
                  <option value="job_applicants">Job Applicants</option>
                </select>
              </div>

              <div className="border-t border-slate-700 pt-6">
                <h4 className="text-lg font-semibold text-white mb-4">LinkedIn Ad Types</h4>
                <div className="space-y-4">
                  <label className="flex items-center gap-3 cursor-pointer">
                    <input
                      type="checkbox"
                      checked={formData.sponsoredContent}
                      onChange={(e) => setFormData({ ...formData, sponsoredContent: e.target.checked })}
                      className="w-5 h-5 bg-slate-900 border-slate-700 rounded text-blue-600 focus:ring-blue-600 focus:ring-offset-slate-800"
                    />
                    <span className="text-slate-300">Sponsored Content</span>
                  </label>

                  <label className="flex items-center gap-3 cursor-pointer">
                    <input
                      type="checkbox"
                      checked={formData.textAds}
                      onChange={(e) => setFormData({ ...formData, textAds: e.target.checked })}
                      className="w-5 h-5 bg-slate-900 border-slate-700 rounded text-blue-600 focus:ring-blue-600 focus:ring-offset-slate-800"
                    />
                    <span className="text-slate-300">Text Ads</span>
                  </label>

                  <label className="flex items-center gap-3 cursor-pointer">
                    <input
                      type="checkbox"
                      checked={formData.messageAds}
                      onChange={(e) => setFormData({ ...formData, messageAds: e.target.checked })}
                      className="w-5 h-5 bg-slate-900 border-slate-700 rounded text-blue-600 focus:ring-blue-600 focus:ring-offset-slate-800"
                    />
                    <span className="text-slate-300">Message Ads (InMail)</span>
                  </label>

                  <label className="flex items-center gap-3 cursor-pointer">
                    <input
                      type="checkbox"
                      checked={formData.dynamicAds}
                      onChange={(e) => setFormData({ ...formData, dynamicAds: e.target.checked })}
                      className="w-5 h-5 bg-slate-900 border-slate-700 rounded text-blue-600 focus:ring-blue-600 focus:ring-offset-slate-800"
                    />
                    <span className="text-slate-300">Dynamic Ads</span>
                  </label>
                </div>
              </div>
            </div>

            <div className="p-6 border-t border-slate-700 flex gap-3 justify-end sticky bottom-0 bg-slate-800">
              <button
                onClick={() => { setShowCreateModal(false); setSelectedCampaign(null); }}
                className="px-6 py-3 bg-slate-700 hover:bg-slate-600 text-white rounded-lg transition-colors"
              >
                Cancel
              </button>
              <button
                onClick={createCampaign}
                className="px-6 py-3 bg-gradient-to-r from-blue-600 to-blue-700 hover:from-blue-700 hover:to-blue-800 text-white rounded-lg transition-all flex items-center gap-2"
              >
                <Plus className="w-5 h-5" />
                {selectedCampaign ? 'Update Campaign' : 'Create Campaign'}
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
